<template>
    <div id="app">
      <h1>TodoList</h1>
      <Header @addTodo="addTodo"/>
      <List :todos="todos"/>
      <Footer :todos="todos" @checkAllTodos="checkAllTodos" @clearTodos="clearTodos"/>
    </div>
</template>

<script>
  import Header from './components/Header'
  import List from './components/List'
  import Footer from './components/Footer'

  export default {
    name: 'App',
    components: {
      Header,
      List,
      Footer
    },
    data(){
      return {
        todos:[
          {id:'001', name:'eat lunch', completed:false},
          {id:'002', name:'sleep', completed:false},
          {id:'003', name:'drive the car', completed:true},
          {id:'004', name:'listen to music', completed:false},
          {id:'005', name:'go jogging', completed:false},
          {id:'006', name:'watch TV', completed:true},
          {id:'007', name:'read a book', completed:true},
        ]
      }
    },
    methods:{
      //添加事件
      addTodo(todoObj){
        console.log('我是addTodo，我被呼叫了', todoObj)
        this.todos.unshift(todoObj)
      },
      //勾選事件
      checkTodo(id){
        this.todos.forEach((todo)=>{
          if(todo.id == id){
            todo.completed = !todo.completed
          }
        })
      },
      //刪除事件
      deleteTodo(id){
        this.todos = this.todos.filter((todo)=>{
          return todo.id != id
        })
      },
      //勾選所有事件
      checkAllTodos(completed){
        this.todos.forEach((todo)=>{
          todo.completed = completed
        })
      },
      //清除所有已選事件
      clearTodos(){
        this.todos = this.todos.filter((todo)=>{
          return todo.completed != true
        })
      }
    },
    mounted(){
      //綁定勾選事件按鈕
      this.$bus.$on('checkTodo', this.checkTodo)
      //綁定刪除事件按鈕
      this.$bus.$on('deleteTodo', this.deleteTodo)
      // this.$bus.$on('clearTodos', this.clearTodos)
    }
  }
</script>

