<template>
    <div v-show="totalNum">
        <input type="checkbox" v-model="isAll"/>
        <span>quantity completed:{{completedNum}} / total quantity:{{totalNum}}</span>
        <button @click="clearTodos">Clear completed events</button>
    </div>
</template>

<script>
    export default {
        name:'Footer',
        props:['todos'],
        computed:{
            //所有事件的總數
            totalNum(){
                return this.todos.length
            },
            //計算被勾選的數量
            completedNum(){
                /* let i = 0
                this.todos.forEach((todo)=>{
                    if(todo.completed) i++
                })
                return i */

                return this.todos.reduce((pre, current)=>{
                    return pre + current.completed
                }, 0)
            },
            isAll:{
                get(){
                    return this.completedNum == this.totalNum && this.totalNum > 0
                },
                set(value){
                    console.log('@@@',value)
                    // this.checkAllTodo(value)
                    this.$emit('checkAllTodos', value)
                }
            }
        },
        methods:{
            //清除所有已選擇的方法
            clearTodos(){
                if(confirm('whether to clear all completed events?')) this.$emit('clearTodos')
            }
        }
    }
</script>

<style scoped>
    div{
        padding: 7px 5px;
        /* font-weight: 700; */
    }
    button{
        font-weight: 700;
        float: right;
    }
</style>