<template>
    <div>
        <input type="text" placeholder="Please enter an event" @keyup.enter="add">
    </div>
</template>

<script>
    import {nanoid} from 'nanoid'
    export default {
        name:'Header',
        // props:['addTodo'],
        methods:{
            add(e){
                if(!e.target.value) return alert("值不可為空")
                const todoObj = {id:nanoid(), name:e.target.value, completed:false}
                console.log(e.target.value)
                this.$emit('addTodo', todoObj, 1, 2)
                e.target.value = ""
            }
        }
    }
</script>

<style scoped>
    input{
        width: 100%;
        margin-bottom: 10px;
        padding: 5px 7px;
        box-sizing: border-box;
        font-weight: 700;
    }
</style>