<template>
    <li>
        <label>
            <input type="checkbox" :checked="todo.completed" @change="handleCheck(todo.id)"/>
            <span> {{todo.name}}</span>
        </label>
        <!-- <button class="editBtn">編輯</button> -->
        <button class="deleteBtn" @click="deleetTodo(todo.id)">remove</button>
    </li>
</template>

<script>
    export default {
        name:'Item',
        props:['todo'],
        methods:{
            handleCheck(id){
                console.log(id)
                // console.log(e.target.checked)
                this.$bus.$emit('checkTodo', id)
            },
            deleetTodo(id){
                if(confirm('Whether to remove the event?')) this.$bus.$emit('deleteTodo', id)
                
            }
        }
    }
</script>

<style  scoped>
    li{
        list-style: none;
        background-color: #FFE153;
        display: flex;
        line-height: 25px;
        padding: 7px;
        /* font-weight: 700; */
        /* border-bottom: 1px solid gray; */
        border: 1px solid #000;
        border-style:dotted;
        margin-top: 5px;
    }
    /* li label{
        float: left;
    } */
    li button{
        float: left;
        margin-left: auto;
        display: none;
        font-weight: 700;
    }
    li:hover{
        background-color: yellow;
    }
    li:hover button{
        display: block;
    }
</style>